export function CleanUp() {
  return () => {
    return
  }
}

export function getContent(): any {
  return document.querySelector('#content')
}

export function scrollToBottom() {
  getContent().scrollToBottom(200)
}

export function scrollToTop() {
  getContent().scrollToTop(200)
}

export const Formatter = new Intl.NumberFormat(Language(), {
  style: 'currency',
  currency: 'USD',
  minimumFractionDigits: 2,
})

export const generateYears = (startYear: number, endYear: number) => {
  var list: Array<number> = []
  for (let i = startYear; i >= endYear; i--) {
    list.push(i)
  }
  return list
}

export const randomNum = (min: number, max: number) => {
  const random = min + Math.random() * (max - min)
  return Number(random)
}
