import React from 'react'
import { IonIcon } from '@ionic/react'
import { useHistory } from 'react-router'
import { star } from 'ionicons/icons'
import moment from 'moment'
export const MovieCard: React.FC<IMovieCardProps> = ({
  id,
  poster_path,
  release_date,
  vote_average,
}) => {
  const history = useHistory()
  return (
    <div
      className="thumbCard"
      onClick={() => history.push(`/movie/${id}`)}
      style={{
        display: 'inline-block',
        width: 'auto',
        padding: '10px',
        borderRadius: '10px',
        marginBottom: '5vh',
      }}
    >
      <img
        className="thumbImg"
        style={{
          width: 'auto',
          objectFit: 'cover',
        }}
        src={'https://image.tmdb.org/t/p/w500/' + poster_path}
        alt={id.toString()}
      />
      <h6 className="thumbDate" style={{}}>
        {moment(release_date).format('MM-DD-YYYY')}
      </h6>
      <h6 className="thumbVote">
        <IonIcon icon={star} />
        {vote_average}
      </h6>
    </div>
  )
}
export interface IMovieCardProps {
  id: any
  poster_path: any
  release_date: any
  vote_average: any
}
