import React from 'react'
import {
  ThemeContext,
  LanguageContext,
  SideMenuContext,
  FirebaseContext,
  SessionContext,
} from '../contexts'
import { ContextsComponentProps } from '../declarations'

export const ContextsComponent: React.FC<ContextsComponentProps> = ({
  Theme,
  Language,
  Menu,
  Firebase,
  useSession,
  children,
}) => {
  let { theme, setTheme } = Theme
  let { language, setLanguage } = Language
  let { showSideMenu, setShowSideMenu } = Menu

  return (
    <>
      <ThemeContext.Provider value={{ theme, setTheme }}>
        <LanguageContext.Provider value={{ language, setLanguage }}>
          <SideMenuContext.Provider value={{ showSideMenu, setShowSideMenu }}>
            <FirebaseContext.Provider value={Firebase}>
              <SessionContext.Provider value={useSession}>
                {/* <OnlineContext.Provider value={Online}> */}
                {children}
                {/* </OnlineContext.Provider> */}
              </SessionContext.Provider>
            </FirebaseContext.Provider>
          </SideMenuContext.Provider>
        </LanguageContext.Provider>
      </ThemeContext.Provider>
    </>
  )
}
