//     | 'anonymous'
//     | undefined
//   >
// }

function FirebaseUI() {
  const firebase = useContext(FirebaseContext)
  const [state, setState] = useState<any>()
  const session: ISessionProps = useContext(SessionContext)
  const { signedIn } = session
  // const history = useHistory()
  useEffect(() => {
    if (
      localStorage.getItem('session') === null ||
      localStorage.getItem('session') === undefined
    ) {
      localStorage.setItem('session', JSON.stringify(state))
    }
  }, [state])
  var uiConfig = {
    callbacks: {
      signInSuccessWithAuthResult: function(authResult: any, redirectUrl: any) { 
        return true
      },
      uiShown: () => {
        let doc: any = document.getElementById('loader')
        doc.style.display = 'none'
      },
    },
    signInFlow: 'popup',  
    signInSuccessUrl: '/home',
    signInOptions: [
      firebase.auth2.GoogleAuthProvider.PROVIDER_ID,
      firebase.auth2.FacebookAuthProvider.PROVIDER_ID,
      //   firebase.auth2.TwitterAuthProvider.PROVIDER_ID,
      //   firebase.auth2.GithubAuthProvider.PROVIDER_ID,
      firebase.auth2.EmailAuthProvider.PROVIDER_ID,
      firebase.auth2.PhoneAuthProvider.PROVIDER_ID,
      firebaseui.auth.AnonymousAuthProvider.PROVIDER_ID,
    ],
    tosUrl: '/terms',
    privacyPolicyUrl: '/privacy',
  }

  const ui =
    firebaseui.auth.AuthUI.getInstance() ||
    new firebaseui.auth.AuthUI(firebase.auth)

  useEffect(() => {
    if (!signedIn) {
      ui.start('#firebase-ui', uiConfig)
    }
  }, [])
  return (
    <div>
      <div id="firebase-ui" />

      <div
        style={{
          width: '100%',
          height: '100%',
          alignItems: 'center',
          alignContent: 'center',
          textAlign: 'center',
        }}
        id="loader"
      >
        <IonSpinner />
      </div>
    </div>
  )
}

export default FirebaseUI
