/* eslint-disable */
import React, { useEffect, useState } from 'react'
import { View } from '../../Components'
import { useParams } from 'react-router-dom'
import {
  IMovieDetail,
  IGenre,
  IProductionCompany,
  ICollection,
} from '../../declarations'
import { useFetchJson } from '../../Utils'
import {
  IonList,
  IonItem,
  IonLabel,
  IonCard,
  IonCardContent,
} from '@ionic/react'

const MovieDetails: React.FC = props => {
  const { id } = useParams()
  const [movie, setMovie] = useState({} as IMovieDetail)
  const [companies, setCompanies] = useState([] as Array<IProductionCompany>)
  const [genres, setGenres] = useState([] as Array<IGenre>)
  const [collection, setCollection] = useState({} as ICollection)
  const [language] = useState(localStorage.getItem('language'))
  const url = `https://api.themoviedb.org/3/movie/${id}?api_key=046fdb0d753c6903e673934705cb553f&language=${language}`
  useEffect(() => {
    useFetchJson(url).then(res => {
      setMovie(res)
      setCompanies(res.production_companies)
      setGenres(res.genres)
      setCollection(res.belongs_to_collection)
    })
  }, [url])
  return (
    <View
      title="iWatchTV"
      backButton={true}
      menuButton={true}
      isSignedIn={true}
      padding={true}
      searchButton={true}
    >
      {/* <Text type="title">Movie Details</Text> */}
      {movie ? (
        <div>
          <h2>{movie?.title}</h2>
          <p>{movie?.release_date}</p>
          <p>{movie?.imdb_id}</p>
          <p>{movie?.popularity}</p>
          <p>{movie?.poster_path}</p>
          <p>{movie?.backdrop_path}</p>
          <p>{movie?.runtime}</p>
          <p>{movie?.overview}</p>
          <p>{movie?.status}</p>
          <p>{movie?.homepage}</p>
          <p>{movie?.original_title}</p>
          <p>{movie?.vote_average}</p>
          <p>{movie?.vote_count}</p>
          <p>{movie?.revenue}</p>
        </div>
      ) : null}
      {genres ? (
        <IonList>
          {genres.map(({ id, name }) => {
            return (
              <IonItem key={id}>
                <IonLabel>{name}</IonLabel>
              </IonItem>
            )
          })}
        </IonList>
      ) : null}
      {companies ? (
        <IonList>
          {companies.map(({ id, name }) => {
            return (
              <IonItem key={id}>
                <IonLabel>{name}</IonLabel>
              </IonItem>
            )
          })}
        </IonList>
      ) : null}
      {collection ? (
        <IonCard>
          <IonCardContent>
            <IonList>
              <IonItem>
                <IonLabel>{collection?.id}</IonLabel>
                <IonLabel>{collection?.name}</IonLabel>
              </IonItem>
            </IonList>
          </IonCardContent>
        </IonCard>
      ) : null}
    </View>
  )
}

export default MovieDetails

interface Props {}
