/* eslint-disable */
import {
  IonContent,
  IonPage,
  IonRefresher,
  IonRefresherContent,
} from '@ionic/react'
import React from 'react'
import { Toolbar } from './'
import { IViewProps } from '../declarations'

const View: React.FC<IViewProps> = ({
  children,
  className,
  title,
  backButton,
  menuButton,
  padding,
  searchButton,
  refresher,
  refreshAction,
  onScrollEvent,
}) => {
  return (
    <IonPage className={'no-scrollbar'}>
      <Toolbar
        title={title}
        backButton={backButton}
        menuButton={menuButton}
        searchButton={searchButton}
      />
      <IonContent
        id="content"
        scrollEvents={true}
        class={
          padding
            ? `ion-padding no-scrollbar ${className}`
            : `no-scrollbar ${className}`
        }
        onIonScroll={onScrollEvent}
      >
        {children}
        {refresher ? (
          <IonRefresher
            onIonPull={e => console.log('loading...')}
            slot="fixed"
            onIonRefresh={refreshAction}
          >
            <IonRefresherContent
              pullingIcon="arrow-dropdown"
              pullingText="Pull to load more"
              refreshingSpinner="circles"
              refreshingText="Loading..."
            ></IonRefresherContent>
          </IonRefresher>
        ) : null}
      </IonContent>
    </IonPage>
  )
}

export default View
