/* eslint-disable */
import {
  IonContent,
  IonHeader,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonMenu,
  IonMenuToggle,
  IonTitle,
  IonToolbar,
  IonItemDivider,
  IonToggle,
} from '@ionic/react'
import React, { useState, useEffect, useContext } from 'react'
import { MenuProps, DeviderProps, ISessionProps } from '../declarations'
import { withRouter } from 'react-router-dom'
import { TranslateText, TranslatedText, isOnline } from '../Utils'
import { Plugins } from '@capacitor/core'
import { sunny, moon, logIn, logOut } from 'ionicons/icons'
import {
  ThemeContext,
  SideMenuContext,
  FirebaseContext,
  SessionContext,
} from '../contexts'
import { useDarkMode } from '../Utils/useDarkMode'

const SideMenu: React.FC<MenuProps> = props => {
  const { title, AppPages, TabPages, Pages } = props
  const { Device } = Plugins
  const theme = localStorage.getItem('theme')
  const menu = localStorage.getItem('menu')
  const [deviceInfo, setDeviceInfo] = useState(Object)
  const session: ISessionProps = useContext(SessionContext)
  const { setTheme } = useContext(ThemeContext)
  const { setShowSideMenu } = useContext(SideMenuContext)
  const firebase = useContext(FirebaseContext)
  const [enabled, setEnabledState] = useDarkMode()
  const detectDevice = async () => await Device.getInfo()
  useEffect(() => {
    detectDevice().then(res => setDeviceInfo(res))
  }, [Device])
  let info = deviceInfo
  const { signedIn } = session
  return (
    <IonMenu contentId="main" type="overlay">
      <IonHeader>
        <IonToolbar>
          <IonTitle>{TranslateText(title)}</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonList>
          {Pages.length >= 1
            ? Pages.map((Page, index) => {
                return (
                  <IonMenuToggle key={index} autoHide={false}>
                    <IonItem
                      routerLink={
                        location.pathname === Page.url ? undefined : Page.url
                      }
                      routerDirection="forward"
                    >
                      <IonIcon slot="start" icon={Page.icon} />
                      <TranslatedText type="label" text={Page.title} />
                    </IonItem>
                  </IonMenuToggle>
                )
              })
            : null}

          {TabPages !== [] ? (
            <>
              <MenuDivider text={TranslateText('Entertainment')} />
              {TabPages.map((tabPage, index) => {
                return (
                  <IonMenuToggle key={index} autoHide={false}>
                    <IonItem
                      routerLink={
                        location.pathname === tabPage.url
                          ? undefined
                          : tabPage.url
                      }
                      routerDirection="forward"
                    >
                      <IonIcon slot="start" icon={tabPage.icon} />
                      <TranslatedText type="label" text={tabPage.title} />
                    </IonItem>
                  </IonMenuToggle>
                )
              })}
            </>
          ) : null}

          {AppPages !== [] ? (
            <>
              <MenuDivider text={TranslateText('App')} />
              {AppPages.map((appPage, index) => {
                return (
                  <IonMenuToggle key={index} autoHide={false}>
                    <IonItem
                      routerLink={
                        location.pathname === appPage.url
                          ? undefined
                          : appPage.url
                      }
                      routerDirection="forward"
                    >
                      <IonIcon slot="start" icon={appPage.icon} />
                      <TranslatedText type="label" text={appPage.title} />
                    </IonItem>
                  </IonMenuToggle>
                )
              })}
              {!signedIn ? (
                <IonMenuToggle key={48} autoHide={false}>
                  <IonItem
                    routerLink={
                      location.pathname === '/login' ? undefined : '/login'
                    }
                    routerDirection="forward"
                  >
                    <IonIcon slot="start" icon={logIn} />
                    <TranslatedText type="label" text={'Log In'} />
                  </IonItem>
                </IonMenuToggle>
              ) : (
                <IonMenuToggle key={94673} autoHide={false}>
                  <IonItem
                    onClick={() => {
                      firebase.doSignOut()
                      localStorage.removeItem('session')
                    }}
                  >
                    <IonIcon slot="start" icon={logOut} />
                    <TranslatedText type="label" text={'Log Out'} />
                  </IonItem>
                </IonMenuToggle>
              )}
            </>
          ) : null}

          <MenuDivider text={TranslateText('Toggles')} />
          <IonItem>
            {theme === 'dark' ? (
              <IonIcon slot="start" icon={sunny} />
            ) : (
              <IonIcon slot="start" icon={moon} />
            )}
            <IonLabel>
              {theme === 'dark'
                ? TranslateText('Light Mode')
                : TranslateText('Dark Mode')}
            </IonLabel>
            <IonToggle
              name="darkMode"
              value="darkMode"
              checked={enabled}
              onIonChange={() => {
                if (enabled) {
                  // localStorage.setItem('theme', 'light')
                  // setTheme(false)
                  setEnabledState(false)
                } else if (!enabled) {

                  setEnabledState(true)
                }
                // if (theme === 'light') {
                //   // localStorage.setItem('theme', 'dark')
                //   // setTheme(true)
                // }
              }}
            />
          </IonItem>

          {info?.operatingSystem === 'ios' ||
          info?.operatingSystem === 'android' ? (
            <></>
          ) : (
            <>
              <IonItem>
                <TranslatedText type="label" text={'Show Side Menu'} />
                <IonToggle
                  name="sideMenu"
                  value="sideMenu"
                  checked={menu === 'on' ? true : false}
                  onIonChange={e => {
                    if (menu === 'on') {
                      localStorage.setItem('menu', 'off')
                      setShowSideMenu(false)
                    }
                    if (menu === 'off') {
                      localStorage.setItem('menu', 'on')
                      setShowSideMenu(true)
                    }
                  }}
                />
              </IonItem>
            </>
          )}
        </IonList>
      </IonContent>
    </IonMenu>
  )
}

export default withRouter(SideMenu)

const MenuDivider: React.FC<DeviderProps> = ({ text }) => {
  return (
    <IonItemDivider>
      <IonLabel>{text}</IonLabel>
    </IonItemDivider>
  )
}
