export class Firebase {
  constructor() {
    try {
      if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig)
      } else {
        firebase.app()
      }
      this.auth = firebase.auth()
      this.auth2 = firebase.auth
      this.firestore = firebase.firestore()
      this.storage = firebase.storage()
      this.analytics = firebase.analytics()
      this.apps = firebase.apps
      this.imagesStorageRef = firebase.storage().ref('images')
    } catch (error) {
      console.log(error)
    }
  }
  getUser = setUser => {
    this.auth.onAuthStateChanged(user => {
      if (user) {
        setUser(user)
      } else {
        setUser(null)
      }
    })
  }
  doSignInWithEmailAndPassword = (email, password, setRes) => {
    this.auth
      .signInWithEmailAndPassword(email, password)
      .then(res => {
        setRes(res.user.email)
        // console.log(res);
        // return res
      })
      .catch(error => {
        setRes(error)
        console.log(error)
        // return error
      })
  }
  doCreateUserWithEmailAndPassword = (user, email, password, setRes) => {
    return this.auth
      .createUserWithEmailAndPassword(email, password)
      .then(res => {
        setRes(res.user)
        if (res.user) {
          res.user
            .updateProfile({
              displayName: user,
              photoURL:
                'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_960_720.png',
            })
            .then(res => console.log('Updated', res))
            .catch(error => console.log(error))
          res.user
            .sendEmailVerification()
            .then(res => console.log(res))
            .catch(error => console.log(error))
        }
        return res
      })
      .catch(error => {
        setRes({ error: true, message: error.message, code: error.code })
        console.log(error)
        return error
      })
  }
  doSignOut = () => {
    this.auth.signOut()
    localStorage.setItem('session', '')
  }
  doPasswordReset = email => this.auth.sendPasswordResetEmail(email)
  doPasswordUpdate = password => this.auth.currentUser.updatePassword(password)
  doVerificationEmail = () => {
    var user = this.auth.currentUser
    return user
      .sendEmailVerification()
      .then(function(res) {
        console.log(res)
      })
      .catch(function(error) {
        console.log(error)
      })
  }
  doUpdatePhotoURL = pic => {
    return this.auth.currentUser
      .updateProfile({ photoURL: pic })
      .then(res => {
        return res
      })
      .catch(error => {
        return error
      })
  }

  doUpdateEmail = email => {
    var user = this.auth.currentUser
    if (email !== '') {
      user
        .updateEmail(email)
        .then(res => {
          return res
        })
        .catch(error => {
          return error
        })
    }
  }
  doReAuthenticateUser = password => {
    let email = this.auth.currentUser.email
    let pass = password
    const credentials = firebase.auth.EmailAuthProvider.credential(email, pass)
    return this.auth.currentUser
      .reauthenticateWithCredential(credentials)
      .then(res => {
        return res
      })
      .catch(error => {
        return error
      })
  }

  doUpdateProfilePic = (password, pic) => {
    this.doReAuthenticateUser(password).then(res => {
      if (res) {
        this.doUpdatePhotoURL(pic)
          .then(res => {
            console.log(res)
          })
          .catch(error => console.log(error))
      }
    })
  }

  doUpdateDisplayName = name => {
    return this.auth.currentUser
      .updateProfile({ displayName: name })
      .then(res => console.log(res))
      .catch(error => console.log(error))
  }
}

const FirebaseContext = createContext(new Firebase())

export default FirebaseContext
