import React from 'react'
import { useFetchJson } from '.'
import { IError, IMovieDetail } from '../declarations'
export function useFetchMovies(
  url: string,
  setError: React.Dispatch<React.SetStateAction<IError>>,
  setMovies: React.Dispatch<React.SetStateAction<IMovieDetail[]>>,
) {
  useFetchJson(url).then(res => {
    if (res.status_code) {
      setError(res)
    } else if (res.results) {
      setMovies(res.results)
    }
  })
}

export function useFetchMoreMovies(
  url: string,
  setError: React.Dispatch<React.SetStateAction<IError>>,
  setMovies: React.Dispatch<React.SetStateAction<IMovieDetail[]>>,
  Movies: IMovieDetail[],
) {
  useFetchJson(url).then(res => {
    if (res.status_code) {
      setError(res)
    } else if (res.results) {
      setMovies(Movies.concat(res.results))
    }
  })
}
