import React from 'react'
import { TranslatedText } from '../../Utils'
import { sortsList, genresList, companiesList } from '.'
import { IonSelect, IonSelectOption, IonChip } from '@ionic/react'
interface ISortingChipsProps {
  yearList?: any | undefined
  setSort?: any | undefined
  setYear?: any | undefined
  setCompanyId?: any | undefined
  setGenre?: any | undefined
  state?: IListsProps | undefined
  actions?: IActionsProps | undefined
}
interface IActionsProps {
  setSort: React.Dispatch<React.SetStateAction<string>>
  setYear: React.Dispatch<React.SetStateAction<string>>
  setCompanyId: React.Dispatch<React.SetStateAction<string>>
  setGenre: React.Dispatch<React.SetStateAction<string>>
}
interface IListsProps {
  sort: string
  year: string
  companyId: string
  genre: string
}
export const SortingChips = ({
  // setSort,
  yearList,
  // setYear,
  // setCompanyId,
  // setGenre,
  state,
  actions,
}: ISortingChipsProps) => {
  let { sort, year, companyId, genre }:any = state
  let { setSort, setYear, setCompanyId, setGenre }:any = actions
  return (
    <div
      className="no-scrollbar center"
      style={{ display: 'flex', overflowX: 'scroll' }}
    >
      <IonChip mode="ios" style={{}}>
        <TranslatedText type="label" text="Sort" />
        <IonSelect
          value={sort}
          onIonChange={e => setSort(e.detail.value)}
          interface="popover"
        >
          <IonSelectOption key={0} value={'popularity.desc'}>
            {'None'}
          </IonSelectOption>
          {sortsList.map(({ id, value, name }) => (
            <IonSelectOption key={id} value={value}>
              {name}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonChip>
      <IonChip mode="ios">
        <TranslatedText type="label" text={'Year'} />
        <IonSelect
          value={year}
          onIonChange={e => setYear(e.detail.value)}
          interface="popover"
        >
          <IonSelectOption key={0} value={''}>
            {'None'}
          </IonSelectOption>
          {yearList.map((year: number) => (
            <IonSelectOption key={year} value={year}>
              {year}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonChip>
      <IonChip mode="ios">
        <TranslatedText type="label" text={'Genres'} />
        <IonSelect
          value={genre}
          onIonChange={e => setGenre(e.detail.value)}
          interface="popover"
        >
          <IonSelectOption key={0} value={''}>
            {'None'}
          </IonSelectOption>
          {genresList.map(({ id, name }) => (
            <IonSelectOption key={id} value={id}>
              {name}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonChip>
      <IonChip mode="ios">
        <TranslatedText type="label" text={'Studios'} />
        <IonSelect
          value={companyId}
          onIonChange={e => setCompanyId(e.detail.value)}
          interface="popover"
        >
          <IonSelectOption key={0} value={''}>
            {'None'}
          </IonSelectOption>
          {companiesList.map(({ id, name }) => (
            <IonSelectOption key={id} value={id}>
              {name}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonChip>
    </div>
  )
}
