/* eslint-disable */
import React, { useState, useEffect } from 'react'
import { Plugins, StatusBarStyle } from '@capacitor/core'
import { Redirect, Route } from 'react-router-dom'
import { IonApp, IonRouterOutlet, IonSplitPane } from '@ionic/react'
import { IonReactRouter } from '@ionic/react-router'
import { Menu, ContextsComponent } from './Components'
import { AppPages, TabPages, Pages } from './contants'
import { routes } from './routes'
import * as Utilities from './Utils'
import { Firebase } from './Firebase/Firebase'
import Home from './Components/Text'
import { Login } from './Pages'
import { SessionContext } from './contexts'
import { useHistory } from 'react-router-dom'
const { Device, StatusBar } = Plugins
const {
  setDefaultLanguage,
  setDefaultTheme,
  isDarkMode,
  isSideMenu,
  isLanguage,
  setDefaultMenu,
  useDeviceInfo,
  ConsoleLog,
  Session,
} = Utilities

const App: React.FC<MainProps> = props => {
  useEffect(() => ConsoleLog({ Type: 'info', Text: 'App Started' }), []) // App Started
  const firebase = new Firebase()
  const [deviceInfo, setDeviceInfo] = useState(Object)
  const [theme, setTheme] = useState(() => isDarkMode())
  const [language, setLanguage] = useState(() => isLanguage())
  const [showSideMenu, setShowSideMenu] = useState(() => isSideMenu())
  
  let info = deviceInfo
  useEffect(() => {
    useDeviceInfo().then(res => setDeviceInfo(res))
  }, [Device])

  useEffect(() => {
    setDefaultTheme(setTheme)
    setDefaultLanguage(setLanguage)
    setDefaultMenu(info?.platform, info?.operatingSystem, setShowSideMenu)
  })

  useEffect(() => {
    info?.operatingSystem === 'ios' || info?.operatingSystem === 'android'
      ? StatusBar.setStyle({
          style: theme ? StatusBarStyle.Dark : StatusBarStyle.Light,
        })
      : ConsoleLog({ Type: 'warning', Text: 'StatusBar not Available' })
  }, [theme])

  
  useEffect(() => ConsoleLog({ Type: 'success', Text: 'App Loaded' }), []) // App Loaded
  return (
    <ContextsComponent
      Theme={{ theme, setTheme }}
      Language={{ language, setLanguage }}
      Menu={{ showSideMenu, setShowSideMenu }}
      Firebase={firebase}
      useSession={Session()}
    >
      <IonApp /* className={theme ? 'dark-theme' : ''} */>
        <IonReactRouter>
          <IonSplitPane when={showSideMenu} contentId="main">
            <Menu
              title="Menu"
              AppPages={AppPages}
              TabPages={TabPages}
              Pages={Pages}
            />
            <IonRouterOutlet id="main" mode="ios">
              {routes.map((route: any) => (
                <Route
                  key={route.path}
                  path={route.path}
                  component={route.component}
                  exact={true}
                />
              ))}

              <Route exact path="/" render={() => <Redirect to="/home" />} />
            </IonRouterOutlet>
          </IonSplitPane>
        </IonReactRouter>
      </IonApp>
    </ContextsComponent>
  )
}

export default App

interface MainProps {
  children: string
}
