import React, { useState } from "react";
import translate from "translate";
import { Text } from "../Components";
import PropTypes from "prop-types";

const TranslatePromise = text => {
  return translate(text, {
    to: LanguageSelected(),
    engine: "yandex",
    key:
      "trnsl.1.1.20200124T014509Z.07fb7b32563f8aa1.fdc714bc2cf3793b3d5e0aa3f9659d446ba9229d"
  });
};

export const TranslateText = text => {
  const [results, setResults] = useState("");
  TranslatePromise(text).then(res => setResults(res));
  return results;
};
TranslateText.propTypes = {
  text: PropTypes.string
};

export const TranslatedText = ({text, type, children}) => {
  const [results, setResults] = useState();
  TranslatePromise(
    text !== undefined ? text : children !== undefined ? children : ""
  ).then(res => setResults(res));
  return <Text type={type}>{results}</Text>;
};
TranslatedText.propTypes = {
  text: PropTypes.string,
  type: PropTypes.oneOf([
    "subtitle",
    "title",
    "skeleton",
    "label",
    "h1",
    "h2",
    "h3",
    "h4",
    "h5",
    "h6",
    "p",
    "b",
    undefined,
    null
  ]),
  children: PropTypes.string
};

export const LanguageSelected = () => {
  const Language = localStorage.getItem("language");
  return Language === "es-ES"
    ? String("es")
    : Language === "fr-FR"
    ? String("fr")
    : Language === "de-DE"
    ? String("de")
    : Language === "it-IT"
    ? String("it")
    : String("en");
};

export const Language = () => {
  const Language = localStorage.getItem("language");
  return Language === "es-ES"
    ? String("es-ES")
    : Language === "fr-FR"
    ? String("fr-FR")
    : Language === "de-DE"
    ? String("de-DE")
    : Language === "it-IT"
    ? String("it-IT")
    : String("en-US");
};

export const language = () => {
  switch (localStorage.getItem('language')) {
    case 'en-US': {
      let language = String('en-US')
      return language
    }
    case 'es-ES': {
      let language = String('es-ES')
      return language
    }
    case 'fr-FR': {
      let language = String('fr-FR')
      return language
    }
    case 'it-IT': {
      let language = String('it-IT')
      return language
    }
    case 'de-DE': {
      let language = String('de-DE')
      return language
    }
    default:
      let language = String('en-US')
      return language
  }
}