import React, { useContext } from 'react'
import { View, Text } from '../Components'
import { FirebaseContext, SessionContext } from '../contexts'
// import firebase from 'firebase'
import * as firebaseui from 'firebaseui'
import { useEffect } from 'react'
import { ConsoleLog, CleanUp } from '../Utils'
import FirebaseUI from '../Firebase/FirebaseUI'
import { IonSpinner } from '@ionic/react'
import { ISessionProps } from '../declarations'
import { useHistory } from 'react-router'
const Login: React.FC<Props> = props => {
  const firebase = useContext(FirebaseContext)
  const session: ISessionProps = useContext(SessionContext)
  const { signedIn } = session
  const history = useHistory()
  useEffect(() => {
    if (signedIn) {
      history.replace('/home')
    }
    CleanUp()
  }, [signedIn])
  return (
    <View
      title="iWatchTV"
      backButton={true}
      menuButton={true}
      isSignedIn={true}
      padding={true}
      searchButton={true}
    >
      <Text type="title">Login</Text>

      <div>
        <FirebaseUI />
        {/* {<div id="firebase-ui" />} */}
      </div>
    </View>
  )
}

export default Login

interface Props {}
