/* eslint-disable */
import React, { useEffect, useContext } from 'react'
import {
  IonHeader,
  IonToolbar,
  IonTitle,
  IonButtons,
  IonButton,
  IonIcon,
  IonMenuButton,
} from '@ionic/react'
import { settings, arrowBack, search } from 'ionicons/icons'
import { useHistory, Link } from 'react-router-dom'
import { IToolbarProps, ISessionProps } from '../declarations'
import { SessionContext } from '../contexts'

const Toolbar: React.FC<IToolbarProps> = ({
  title,
  backButton,
  menuButton,
  searchButton,
}) => {
  const history = useHistory()
  const { signedIn }: ISessionProps = useContext(SessionContext)
  return (
    <IonHeader mode="ios">
      <IonToolbar mode="ios">
        <IonButtons slot="start">
          {menuButton ? <IonMenuButton /> : null}
          {backButton ? (
            <IonButton
              onClick={e => {
                e.preventDefault()
                history.goBack()
              }}
              mode="ios"
            >
              <IonIcon mode="ios" icon={arrowBack} />
            </IonButton>
          ) : null}
        </IonButtons>
        <a href="/home">
          <IonTitle translate="yes">{title}</IonTitle>
        </a>
        <IonButtons slot="end">
          {signedIn ? (
            <IonButton
              routerLink={
                location.pathname === '/settings' ? undefined : '/settings'
              }
              routerDirection="forward"
              mode="ios"
            >
              <IonIcon mode="ios" icon={settings} />
            </IonButton>
          ) : null}
          {searchButton ? (
            <IonButton
              routerLink={
                location.pathname === '/search' ? undefined : '/search'
              }
              routerDirection="forward"
              mode="ios"
            >
              <IonIcon mode="ios" icon={search} />
            </IonButton>
          ) : null}
        </IonButtons>
      </IonToolbar>
    </IonHeader>
  )
}

export default Toolbar
