import React from 'react'
import { IConsoleLogProps } from '../declarations'

const DetectType = (Type: string) => {
  return Type === 'alert'
    ? '[ALERT]'
    : Type === 'help'
    ? '[HELP]'
    : Type === 'info'
    ? '[INFO]'
    : Type === 'notification'
    ? '[NOTIFICATION]'
    : Type === 'todo'
    ? '[TODO]'
    : Type === 'warning'
    ? '[WARNING]'
    : Type === 'error'
    ? '[ERROR]'
    : Type === 'success'
    ? '[SUCCESS]'
    : ''
}

export function ConsoleLog({ Type, Text }: IConsoleLogProps) {
  // const [type, setType] = useState<any>()
  const type = DetectType(Type)
  console.log(`${type}: ${Text}`)
}

export const ConsoleLogComponent: React.FC<IConsoleLogProps> = ({
  Type,
  Text,
}) => {
  const type = DetectType(Type)
  console.log(`${type}: ${Text}`)
  return <></>
}
