const setDefaultMenu = (platform: string, os: string, setShowSideMenu: any) => {
  let menu = localStorage.getItem('menu')
  const isAndroid = platform === 'android'
  const isiOS = platform === 'ios' && os === 'ios'
  const isWeb = platform === 'web'
  const isElectron = platform === 'electron'
  if ((isAndroid || isiOS) && (os === 'ios' || os === 'android')) {
    if (menu !== 'off' || menu === null || menu === undefined) {
      console.log('Menu Off')
      localStorage.setItem('menu', 'off')
      setShowSideMenu(false)
    }
  }
  if ((isWeb || isElectron) && os !== 'ios' && os !== 'android') {
    if (menu === null || menu === null) {
      console.log('Menu On')
      localStorage.setItem('menu', 'on')
      setShowSideMenu(true)
    }
  }
  if ((isWeb || isElectron) && (os === 'ios' || os === 'android')) {
    if (menu === 'on' || menu === null || menu === null) {
      console.log('Menu Off')
      localStorage.setItem('menu', 'off')
      setShowSideMenu(false)
    }
  }
}

export default setDefaultMenu

interface Props {
  platform: string
  os: string
}
