/* eslint-disable */
import React, { useEffect, useState } from 'react'
import { View } from '../../Components'
import {
  TranslatedText,
  CleanUp,
  useFetchMovies,
  useFetchMoreMovies,
  generateYears,
  isLanguage,
  useAuth,
  randomNum,
  scrollToBottom,
  scrollToTop,
} from '../../Utils'
import { IError, IMovieDetail } from '../../declarations'
import {
  IonSpinner,
  IonIcon,
  IonList,
  IonToolbar,
  IonButtons,
  IonContent,
  IonFabButton,
  IonFab,
} from '@ionic/react'
import { useLocation, useHistory } from 'react-router'
// import { IonContent, IonRefresher, IonRefresherContent } from '@ionic/react'
import { RefresherEventDetail } from '@ionic/core'
import { useWindowSize } from '../../Utils'
import { MoreCard, SortingChips, MovieCard } from './'
import { IonInfiniteScroll, IonInfiniteScrollContent } from '@ionic/react'
import { useIonViewWillEnter } from '@ionic/react'
import { add, arrowRoundDown, arrowRoundUp } from 'ionicons/icons'

const Movies: React.FC<MainProps> = props => {
  var ionInfiniteScrollRef: React.RefObject<HTMLIonInfiniteScrollElement>
  ionInfiniteScrollRef = React.createRef<HTMLIonInfiniteScrollElement>()
  const [language] = useState(isLanguage())
  const [Error, setError] = useState<IError>(Object)
  const [Movies, setMovies] = useState<IMovieDetail[]>([])
  const location = useLocation()
  const history = useHistory()
  const isMOVIES = Movies.length > 0
  const isERROR = Error ? true : false

  const size = useWindowSize()
  const auth = useAuth()

  const [disableInfiniteScroll, setDisableInfiniteScroll] = useState<boolean>(
    false,
  )

  const [page, setPage] = useState(1)
  const [sort, setSort] = useState('popularity.desc')
  const [year, setYear] = useState('')
  const [companyId, setCompanyId] = useState('')
  const [genre, setGenre] = useState('')

  const yearList: any = generateYears(2026, 1880)
  const pages = (page: number) => (page > 0 ? '&page=' + page : '')
  const languages = (language: string | null) =>
    language !== '' ? '&language=' + language : ''
  const sorts = (sort: string) => (sort !== '' ? '&sort_by=' + sort : '')
  const realease_year = (year: string) =>
    year !== '' ? '&primary_release_year=' + year : ''
  const companies = (companyId: string) =>
    companyId !== '' ? '&with_companies=' + companyId : ''
  const genres = (genre: string) =>
    genre !== '' ? '&with_genres=' + genre : ''

  let url: string = `https://api.themoviedb.org/3/discover/movie?api_key=046fdb0d753c6903e673934705cb553f${sorts(
    sort,
  )}${pages(page)}${languages(language)}${realease_year(year)}${companies(
    companyId,
  )}${genres(genre)}`

  useEffect(() => {
    let state: any = location.state
    if (state?.id !== undefined && state?.path.includes('/movie/')) {
      setCompanyId(state.id)
    }
    CleanUp()
  })

  function doRefresh(event: CustomEvent<RefresherEventDetail>) {
    // history.replace(location.pathname)
    setTimeout(() => {
      event.detail.complete()
    }, 100)
    window.location.reload()
  }

  useIonViewWillEnter(() => {
    setDisableInfiniteScroll(true)
  })

  useEffect(() => {
    useFetchMovies(url, setError, setMovies)
    useFetchMoreMovies(url, setError, setMovies, Movies)
    setDisableInfiniteScroll(false)
  }, [])

  useEffect(() => {
    useFetchMoreMovies(url, setError, setMovies, Movies)
    CleanUp()
  }, [page])

  useEffect(() => {
    setPage(1)
    useFetchMovies(url, setError, setMovies)
    CleanUp()
  }, [sort, year, companyId, genre])

  async function searchNext($event: CustomEvent<void>) {
    setPage(page + 1)
    // await useFetchMoreMovies(url, setError, setMovies, Movies)
    setDisableInfiniteScroll(false)
    ;($event.target as HTMLIonInfiniteScrollElement).complete()
  }

  return (
    <View
      className="center no-scrollbar"
      title="iWatchTV"
      backButton={true}
      menuButton={true}
      isSignedIn={true}
      padding={false}
      searchButton={true}
      // refresher={true}
      // refreshAction={doRefresh}
    >
      <TranslatedText type="title" text="Movies" />
      <IonToolbar className="sticky-top">
        <div>
          {isMOVIES ? (
            <SortingChips
              yearList={yearList}
              actions={{
                setSort: setSort,
                setYear: setYear,
                setCompanyId: setCompanyId,
                setGenre: setGenre,
              }}
              state={{
                sort: sort,
                year: year,
                companyId: companyId,
                genre: genre,
              }}
            />
          ) : null}
        </div>
      </IonToolbar>
      {isMOVIES
        ? Movies?.map(({ id, poster_path, release_date, vote_average }) =>
            poster_path !== null ? (
              <MovieCard
                key={id + randomNum(1, 999)}
                id={id}
                poster_path={poster_path}
                release_date={release_date}
                vote_average={vote_average}
              />
            ) : null,
          )
        : null}
      {/* {isMOVIES ? <MoreCard page={page} setPage={setPage} /> : <IonSpinner />} */}

      <IonInfiniteScroll
        style={{ marginTop: '-30px' }}
        ref={ionInfiniteScrollRef}
        // position="bottom"
        threshold="10%"
        disabled={false}
        onIonInfinite={(e: CustomEvent<void>) => searchNext(e)}
      >
        <IonInfiniteScrollContent
          loadingSpinner="bubbles"
          loadingText="Loading more.."
        ></IonInfiniteScrollContent>
      </IonInfiniteScroll>
      <IonFab vertical="bottom" horizontal="end" slot="fixed">
        <IonFabButton onClick={() => scrollToTop()}>
          <IonIcon icon={arrowRoundUp} />
        </IonFabButton>
      </IonFab>
    </View>
  )
}

export default Movies

interface MainProps {}
