import React from "react";
import {
    IonCardTitle,
    IonCardSubtitle,
    IonSkeletonText,
    IonText,
    IonLabel
} from "@ionic/react";
import { TextTypes } from "../declarations";

const Home: React.FC<Props> = ({ children, type }) => {
    return type === "subtitle" ? (
        <IonCardSubtitle>{children}</IonCardSubtitle>
    ) : type === "title" ? (
        <IonCardTitle>{children}</IonCardTitle>
    ) : type === "skeleton" ? (
        <IonSkeletonText />
    ) : type === "label" ? (
        <IonLabel>{children}</IonLabel>
    ) : type === "h1" ? (
        <h1>{children}</h1>
    ) : type === "h2" ? (
        <h2>{children}</h2>
    ) : type === "h3" ? (
        <h3>{children}</h3>
    ) : type === "h4" ? (
        <h4>{children}</h4>
    ) : type === "h5" ? (
        <h5>{children}</h5>
    ) : type === "h6" ? (
        <h6>{children}</h6>
    ) : type === "p" ? (
        <p>{children}</p>
    ) : type === "b" ? (
        <b>{children}</b>
    ) : (
                                                        <IonText>{children}</IonText>
                                                    );
};
interface Props {
    type: TextTypes;
    children: React.ReactNode;
}
export default Home;
